import { Stack } from '@fluentui/react';
import React from 'react';
import appIcon from '../assets/appIcon.svg';
import { useAppHeaderStyles } from './AppHeader.styles';
export interface AppHeaderProps {
    title?: string;
    items?: JSX.Element;
    farItems?: JSX.Element;
    moduleName?: string;
}

export const AppHeader: React.FC<AppHeaderProps> = ({ items, farItems, title }) => {
    const headerStyles = useAppHeaderStyles();
    return (
        <Stack as='header' horizontal disableShrink horizontalAlign='space-between' className={headerStyles.root}>
            <img src={appIcon} style={{ width: ' 36px' }} />
            <h2 className={headerStyles.text}>{title}</h2>
            <Stack verticalAlign='center' grow>
                {items}
            </Stack>
            <Stack horizontalAlign='center' verticalAlign='center'>
                {farItems}
            </Stack>
        </Stack>
    );
};
